import React, { Component } from 'react';
import {Menu, Image, Icon, Modal, Form, Button} from 'semantic-ui-react'
import {Link} from "react-router-dom";

class TopMenu extends Component {

  constructor(props){
    super(props);
    this.state = {
      isModalVisible: false,
      inputLogin: '',
      inputPassword: '',
      doublePassword: '',
      inputMail: ''
    }
  }

  showModal = () =>{
      let state = this.state.isModalVisible;
      this.setState({
        isModalVisible: !state
      },() => console.log(this.state.isModalVisible) )
  }

    changeLogin = (event) =>{
      this.setState({
        inputLogin: event.target.value
      })
    }

    changePassword = (event) =>{
      this.setState({
        inputPassword: event.target.value
      })
    }

    changeMail = (event) =>{
      this.setState({
        inputMail: event.target.value
      })
    }

    changeDoublePassword = (event) =>{
      this.setState({
        doublePassword: event.target.value
      })
    }    

  render() {
    return (
        <Menu className="top-menu">
          <Menu.Item>
            <Link to="/"></Link>
          </Menu.Item>
          <Menu.Item>
            <Link to="/vocabulary"></Link>
          </Menu.Item>          
          <Menu.Item>
            <Link to="/grammar"></Link>
          </Menu.Item>
          <Menu.Item>
            <Link to="/training"></Link>
          </Menu.Item>
          <Menu.Item>
            <Link to="/tests"></Link>
          </Menu.Item>
          <Menu.Item>
            <Link to="/materials"></Link>
          </Menu.Item>
          <Menu.Item>
            <Link to="/books"></Link>
          </Menu.Item>           
          {/*
            <Menu.Item>
              <Link to="/statistics"></Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/audioplayer"></Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/settings"></Link>
            </Menu.Item> 
          */}         
             
      

        {/*
          <Menu.Item className="login-item">
            <Modal className="modal-form login-modal" trigger={<Button primary></Button>} closeIcon>
              <Form className="input-form login-form">
                <Form.Field>
                  <label> </label>
                  <input value={this.state.inputLogin} onChange={this.changeLogin} type="text" />
                </Form.Field>
                <Form.Field>
                  <label> </label>
                  <input value={this.state.inputPassword} onChange={this.changePassword} type="text" />
                </Form.Field>
                <Button primary onClick={this.finishLogin}></Button>
              </Form>         
            </Modal>              
          </Menu.Item>
          <Menu.Item className="login-item">
            <Modal className="modal-form login-modal" trigger={<Button primary></Button>} closeIcon>
              <Form className="input-form login-form">
                <Form.Field>
                  <label> </label>
                  <input value={this.state.inputLogin} onChange={this.changeLogin} type="text" />
                </Form.Field>
                <Form.Field>
                  <label> e-mail</label>
                  <input value={this.state.inputMail} onChange={this.changeMail} type="text" />
                </Form.Field>
                <Form.Field>
                  <label> </label>
                  <input value={this.state.inputPassword} onChange={this.changePassword} type="text" />
                </Form.Field>                   
                <Form.Field>
                  <label> </label>
                  <input value={this.state.doublePassword} onChange={this.changeDoublePassword} type="text" />
                </Form.Field>
                <Button primary onClick={this.finishRegistration}></Button>
              </Form>         
            </Modal>              
          </Menu.Item>                                             
          <Menu.Item position="right" className="menu-image" >
            {this.state.isModalVisible ?
              <div className="user-modal">
                <div className="user-modal-pic">
                  <Image src="/icons/member.png" />              
                  <div className="user-modal-name">
                    <h5> User</h5>
                    <h6>blabla@mail.com</h6>
                  </div>                
                </div>
                <p><Link to="/statistics"> <Icon name='chart bar outline' size='' /></Link></p>
                <p><Link to="/settings"> <Icon name='setting' size=''  /></Link></p>
                <p><Link to="/statistics"> <Icon name='log out' size=''  /></Link></p>
              </div> : null
            }
            <Image className="user-avatar" onClick={this.showModal} src="/icons/member.png" />
          </Menu.Item> 
        */}
                                                                                                    
        </Menu>
 	);
  }
}


export default TopMenu;